import { Component, OnInit, Injector, ViewChild, TemplateRef, HostListener } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { MatDialog } from '@angular/material/dialog';
import { MatDialogRef } from "@angular/material/dialog";

import { VendorService } from 'src/app/api/controllers/Vendor';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from '../../../../shared/services/shared.service';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';

@Component({
  selector: 'app-all-vendors',
  templateUrl: './all-vendors.component.html',
  styleUrls: ['./all-vendors.component.scss']
})
export class AllVendorsComponent implements OnInit {

  vendorCategoryData: any;
  vendorCategoryId: string = "All";

  isVendorCategoryLoaded: boolean = false;

  isVendorCategorySelected: boolean = false;
  isVendorLoaded: boolean = true;

  vendorData: string = "";
  unitFieldType: string = "unitno";
  unitOrder: boolean = true;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 10;
  totalFilterItems: any;

  modalService: any;

  vendor: any;

  vendorListData: any;

  categoryName: string = "";

  selectedInput: string = "";
  columnField: any = {};

  public columnData: any;

  gridSourceData: any;

  public vendorTableList: Array<any> = [];

  @ViewChild("viewVendorRef", { static: false }) viewVendorRef: TemplateRef<any>;

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  @HostListener('window:onEditVendor', ['$event.detail'])
  onEditVendor(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let vendorId = dataRecord.vendorId;
    this.router.navigateByUrl('/ams/vendor/edit-vendor/' + vendorId);
  }


  @HostListener('window:onVendorDelete', ['$event.detail'])
  onVendorDelete(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let vendorId = dataRecord.vendorId
    this.modalService.showConfirmModal(vendorId);
  }

  @HostListener('window:onViewVendor', ['$event.detail'])
  onViewVendor(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let vendorId = dataRecord.vendorId
    this.showVendorInfo(vendorId)
    // this.router.navigateByUrl('/ams/vendor/edit-vendor/' + vendorId);
  }

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private injector: Injector,
    public dialog: MatDialog,
    private vendorService: VendorService,
    private lookupService: LookupService,
    private sharedService: SharedService,
    private cookieService: CookieService
  ) {
    this.modalService = this.injector.get(ModalService);
  }

  vendorPrint(vendor) {
    this.dialog.open(this.viewVendorRef, {
      panelClass: 'material',
      disableClose: true,
      data: vendor
    });

  }

  getPrintParams(event){
    this.datagrid.exportdata(event, 'vendorData');
  }

  onGlSearchFilter() {

    if(this.vendorData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.vendorData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }
  getIndexParams(event) {
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
  }

  sortUnitData(type) {
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if (this.unitFieldType == type) {
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  selectColInput(value) {
    this.selectedInput = value;
  }

  onSelectChange(event, type, name) {

    if (!_.isEmpty(event)) {
      this.selectedInput = type;
      this.columnField[type] = event[name];
    }
    else {
      this.columnField = {};
    }

  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }


  getVendorCategoryName(id) {

    _.each(this.vendorCategoryData, (item, index) => {

      if (item.lookupValueId == parseInt(id)) {
        this.categoryName = item.lookupValueName;
      }

      this.isVendorCategorySelected = true;


    });

    if (id == "All") {
      this.categoryName = "";
    }

    this.isVendorLoaded = false;

    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    this.vendorService.getVendorByApartmentId(params).subscribe((res: any) => {

      if (id != "All") {
        this.vendorListData = res.filter(item => {
          return (item.isActive && item.vendorCategoryId == parseInt(id));
        });
        let arrangeSlotRawData = {
          localdata: this.vendorListData,
          datatype: "array"
        }
        this.vendorTableList = new jqx.dataAdapter(arrangeSlotRawData);
      }
      else {
        this.vendorListData = res.filter(item => {
          return item.isActive;
        });
        let arrangeSlotRawData = {
          localdata: this.vendorListData,
          datatype: "array"
        }
        this.vendorTableList = new jqx.dataAdapter(arrangeSlotRawData);
       
      }

      this.isVendorLoaded = true;

      this.totalItems = this.vendorListData.length;

      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }

      else {
        this.ItemEndIndex = this.totalItems;
      }



    },
      error => {

      });

  }

  showDeleteConfirmModal(id) {
    this.modalService.showConfirmModal(id);
  }

  showVendorInfo(vendor) {

    // this.dialog.open(this.viewVendorRef, {
    //   panelClass: 'material',
    //   disableClose: true,
    //   data: vendor
    // });

  }

  isNoItemsAvailable() {
    return this.totalItems == 0 ? true : false;
  }

  ngOnInit() {

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }

    this.columnData = [{
      text: 'Vendor ID',
      datafield: 'vendorId',
      width: 100,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'Contact Person',
      datafield: 'contactPerson',
      minwidth: 100,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Phone No',
      datafield: 'phone1',
      width: 100,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'VAT No',
      datafield: 'vatorTin',
      minwidth: 100,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'Tax1 No',
      datafield: 'tax1',
      minwidth: 100,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Tax2 No',
      datafield: 'tax2',
      width: 100,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'Tax3 No',
      datafield: 'tax3',
      minwidth: 100,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'Tax4 No',
      datafield: 'tax4',
      minwidth: 100,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'action',
      cellsalign: 'center',
      align: 'center',
      width: 120,
      cellsrenderer: (row): string => {
        return '<div class="simple-actions"><a href="javascript:void(0)" class="mr-2" #v onClick="viewVendor(' + row + ')"><i class="fa fa-eye icon view" aria-hidden="true"></i></a><a href="javascript:void(0)" class="mr-2" onClick="editVendorEvent(' + row + ')"><i class="fa fa-pencil icon edit" aria-hidden="true"></i></a><a href="javascript:void(0)" class="mr-2" onClick="showConfirmDeleteEvent(' + row + ')"><i class="fa fa-trash icon delete" aria-hidden="true"></i></a></div>'
     
      },
      renderer: columnrenderer
    }
  ];

    let params = {
      LookupTypeId: 57
    }

    this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res: any) => {

      this.isVendorCategoryLoaded = true;

      this.vendorCategoryData = res.filter(item => {
        return item.isActive;
      });

      this.getVendorCategoryName(this.vendorCategoryId);


      if (this.route.params['value'].id != undefined) {

        this.vendorCategoryId = "" + this.route.params['value'].id;
        this.getVendorCategoryName(this.vendorCategoryId);

      }

    });




    // delete vendor
    this.sharedService.unitlistdeleteindexcast.subscribe(id => {

      if (id != null) {

        var params = {
          vendorId: id,
          deleteBy: parseInt(this.cookieService.get('userId'))
        }


        this.vendorService.deleteVendor(params).subscribe((res: any) => {

          _.each(this.vendorListData, (type) => {
            if (type.vendorId == id) {
              type.isActive = false;
            }
          })

          setTimeout(() => {

            this.vendorListData = this.vendorListData.filter((type) => type.vendorId !== id);
            this.totalItems = this.vendorListData.length;
            this.sharedService.setAlertMessage("Vendor deleted Successfully");
            this.sharedService.setUnitListDeleteIndex(null);

          }, 500)


        }, error => {
          console.log(error);
        });


      }

    });


  }


}

function viewVendor(row) {
  var event = new CustomEvent('onViewVendor', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).viewVendor = viewVendor;

function showConfirmDeleteEvent(row) {
  var event = new CustomEvent('onVendorDelete', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).showConfirmDeleteEvent = showConfirmDeleteEvent;


function editVendorEvent(row) {
  var event = new CustomEvent('onEditVendor', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editVendorEvent = editVendorEvent;
